.intro {
  color: @white;
  text-align: center;
  background-image: url("../img/green-cat-main.jpg");
  background-repeat: no-repeat;
  background-position: center;
  min-width: 320px;
  background-color: @special_dark-green;
  height: 302px;

  @media (min-resolution: 2dppx) {
    .promo {
      background-image: url("../img/green-cat-main@2x.jpg");
    }
  }
}

.intro__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  text-align: center;
  padding: 26px 35px 1px 35px;
  margin-top: 0;
  margin-bottom: 24px;
}

.intro__slogan {
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  margin-bottom: 28px;
}

.intro__selection-link {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  text-transform: uppercase;
  align-items: center;
  text-align: center;
  padding: 10px 44px;
  margin-top: 5px;
  background-color: @basic_green;
  text-decoration: none;
  color: @white;
  width: 60%;
  display: inline-block;

  &:hover,
  &:focus {
    background-color: @special_dark-green;
  }

  &:active {
    color: rgba(@white, 0.3);
  }
}

.intro__can-image {
  display: flex;
  margin: 0 auto;
}

@media (min-width: @tablet-width) {
  .intro {
    background-image: none;
    background-color: transparent;
    color: @black;
    display: flex;
    flex-direction: column;
    width: 710px;
    min-height: 650px; //   //
    position: relative;
  }

  .intro__title {
    font-size: 60px;
    line-height: 60px;
    font-weight: 400;
    width: 416px;
    padding: 0;
    margin-left: 117px;
    margin-bottom: 41px;
    margin-top: 65px;
    order: 0;
    text-align: start;
  }

  .intro__slogan {
    font-size: 20px;
    line-height: 20px;
    order: 0;
    text-align: left;
    padding: 0;
    margin-left: 119px;
    margin-top: 0;
    margin-bottom: 49px;
  }

  .intro__selection-link {
    font-size: 20px;
    line-height: 20px;
    order: 1;
    padding: 16px 26px;
    width: 192px;
    position: absolute;
    left: 120px;
    top: 292px;
  }

  .intro__can-image {
    order: 2;
    margin-left: 30px;
    margin-top: -28px;
    z-index: 0;
    width: 709px;
    height: 609px;
  }
}

@media (min-width: @desktop-width) {
  .intro {
    width: 100%;
    min-height: 550px;
  }

  .intro__can-image {
    z-index: 5;
    width: 552px;
    height: 499px;
    margin: 0;
    margin-left: 558px;
    margin-top: -310px;
  }

  .intro__title {
    margin-left: 190px;
    margin-top: 110px;
  }

  .intro__slogan {
    margin-left: 190px;
  }

  .intro__selection-link {
    margin-left: 70px;
    top: 338px;
  }
}
